.listContainer {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.listWrapper {
    width: 100%;
    max-width: 1024px;
    display: flex;
    gap: 20px;
}

.listSerach {
    flex: 1;
    background-color: #febb02;
    padding: 10px;
    border-radius: 10px;
    position: sticky;
    top: 10px;
    height: max-content;
}
.lsTitle{
    font-size: 20px;
    color: #282727;
    margin-bottom: 10px;
  }

  .lsItem {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-bottom: 10px;
  }

  .lsItem>label{
    font-size: 12px;

  }
  .lsItem>input{
   height: 30px;
   border: none;
   padding: 5px;

    
  }
  .lsItem>span{
    height: 30px;
    background-color: white;
    padding: 5px;
    display: flex;
    cursor: pointer;
    align-items: center;

    
     
   }

   .lsOptions{
    padding: 10px;
  }
  
  .lsOptionItem{
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    color: #555;
    font-size: 12px;
  }
  
  .lsOptionInput{
    width: 50px;
  }
  
  .listSerach>button{
    padding: 10px;
    background-color: #0071c2;
    color: white;
    border: none;
    width: 100%;
    font-weight: 500;
    cursor: pointer;
  }
  
.listResult {
    flex: 3;
}

