.navbar {
    height: 50px;
    background-color: #003b95;;
    display: flex;
    justify-content: center;
}

.navContainer {
    width: 100%;
    max-width: 1024px;
    color: rgb(255, 255, 255);
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.logo {
    font-weight: 500;
}

.navButton{
    margin-left: 20px;
    border: none;
    padding: 5px 10px;
    cursor: pointer;
    color: #003580;
}